.accordion {
    width: 450px;
    margin: 10px auto 30px auto;
}

.accordion-2 {
    width: 450px;
    margin: 10px auto 30px auto;
}

.accordion-item {
    border-radius: 10px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    max-width: 550px;
    margin-bottom: 12px;
}

.accordion-item-header {
    padding: 0.5rem 3rem 0.5rem 1rem;
    min-height: 3.5rem;
    line-height: 1.25rem;
    display: flex;
    align-items: center;
    position: relative;
    border-radius: 10px;
    cursor: pointer;
    box-shadow: 0px 5px 10px #E0E0E0;
}

.accordion-item-header-2 {
    padding: 0.5rem 3rem 0.5rem 1rem;
    min-height: 3.5rem;
    line-height: 1.25rem;
    display: flex;
    align-items: center;
    position: relative;
    border-radius: 10px;
    cursor: pointer;
    box-shadow: 0px 5px 10px #E0E0E0;
}

.accordion-item-header::after {
    content: "\005E";
    transform: rotate(90deg);
    font-size: 15px;
    position: absolute;
    right: 1rem;
    transition: transform 0.2s ease-in-out;
    top: 25px;
}

.accordion-item-header.active::after {
    transform: rotate(182deg);
    top: 25px;
}

.accordion-item-body {
    max-height: 0;
    overflow: hidden;
    box-shadow: 0px 25px 59px 0px rgba(191, 191, 236, 0.72);
    transition: max-height 0.2s ease-out;
}

.accordion-item-body-content {
    padding: 1rem;
    line-height: 1.5rem;
    padding: 34px 27px 37px 29px;
    bottom: 1px solid black;
    background: #F5F5FF;
    border-radius: 10px;
}

.accordion-item-header.active+.accordion-item-body {
    max-height: 500px;
    margin-bottom: 70px;
}

